import { handleNavigation } from './handlers';
import { navbarList, body } from './refs';
import iziToast from 'izitoast';

// —Ñ-—è –±–ª–æ–∫—É–≤–∞–Ω–Ω—è —Å–∫—Ä–æ–ª—É –Ω–∞ –±–æ–¥—ñ - –¥–æ–¥–∞—î –∫–ª–∞—Å —Å—Ç–∏–ª—ñ–≤
export function stopScroll() {
  body.classList.toggle('no-scroll');
}

// –§—É–Ω–∫—Ü—ñ—ó HandleScreenChange —Ç–∞ OnNavClick –∑—Ä–æ–±–ª–µ–Ω—ñ –¥–ª—è —Ç–æ–≥–æ,
// —â–æ–± –ø–µ—Ä–µ—Ö—ñ–¥ –ø–æ —Å–µ–∫—Ü—ñ—è–º –≤ Desktop-—ñ —Ç–∞ Tablet-–≤–µ—Ä—Å—ñ—ó –≤—ñ–¥–±—É–≤–∞–≤—Å—è –ø–æ —è–∫–æ—Ä–Ω–∏–º –ø–æ—Å–∏–ª–∞–Ω–Ω—è–º,
// –∞ –≤ –º–æ–±—ñ–ª—å–Ω—ñ–π –≤–µ—Ä—Å—ñ—ó –≤—ñ–¥–ø—Ä–∞—Ü—å–æ–≤—É–ª–∞ —Ñ—É–Ω–∫—Ü—ñ—è HandleNavigation.

// —Ñ-—è –ø–µ—Ä–µ–≤—ñ—Ä—è—î —Ä–æ–∑–º—ñ—Ä –µ–∫—Ä–∞–Ω—É, —è–∫—â–æ –≤—ñ–Ω –≤—ñ–¥–ø–æ–≤—ñ–¥–∞—î —É–º–æ–≤—ñ (max-width:767px)
// —Ç–æ —Å—Ç–∞–≤–∏—Ç—å —Å–ª—É—Ö–∞—á–∞ –ø–æ–¥—ñ—ó –Ω–∞ –Ω–∞–≤–±–∞—Ä, –≤ —ñ–Ω—à–æ–º—É —Ä–∞–∑—ñ ‚Äî –∑–Ω—ñ–º–∞—î
export function handleScreenChange(e) {
  if (e.matches) {
    navbarList.addEventListener('click', onNavClick);
  } else {
    navbarList.removeEventListener('click', onNavClick);
  }
}

// –≤–∏–∫–ª–∏–∫–∞—î —Ñ-—é –Ω–∞–≤—ñ–≥–∞—Ü—ñ—ó –º–æ–±—ñ–ª—å–Ω–æ—ó –≤–µ—Ä—Å—ñ—ó
export function onNavClick(event) {
  handleNavigation(event);
}

export function toTitleCase(str) {
  return str.toLowerCase().replace(/\b\w/g, char => char.toUpperCase());
}

export function showInfoToast(message) {
  iziToast.info({
    position: 'topRight',
    message: message,
  });
}

export function showSuccessToast(message) {
  iziToast.success({
    position: 'topRight',
    message: message,
  });
}

export function getBooksSelected() {
  return Number(refs.bookModalCountInput.value);
}